import React from 'react';
import { nanoid } from 'nanoid';
import { connect } from 'react-redux';
import {
  updataCards,
  updataPastime,
  updataVerifiableCard,
  showCards,
  disableCards,
  disableAllCards,
  enableAllCards,
  hideCards,
  increaseNumberMatches,
  openResultPopup,
  openWinPopup,
} from '../../redux/actions';
import { emojiArray } from '../../utils/constants';
import { msToTime } from '../../utils/msToTime';
import Card from '../Card/Card';
import Button from '../Button/Button';
import ResultPopup from '../ResultPopup/ResultPopup';
import WinPopup from '../WinPopup/WinPopup';
import './App.css';

function App({
  cards,
  updataCards,
  disabledCards,
  pastTime,
  verifiable–°ard,
  numberMatches,
  updataPastime,
  updataVerifiableCard,
  showCards,
  disableCards,
  disableAllCards,
  enableAllCards,
  hideCards,
  increaseNumberMatches,
  openResultPopup,
  openWinPopup,
}) {
  const [intervalTime, setIntervalTime] = React.useState(null);
  const [cardHideTimer, setCardHideTimer] = React.useState(null);

  function startGame() {
    updataCards(getArrayToDraw(emojiArray));
    enableAllCards();
    clearInterval(intervalTime);
    updataPastime(0);
    const startDate = new Date();
    const newIntervalTime = setInterval(() => {
      updataPastime(new Date() - startDate);
    }, 1000);
    setIntervalTime(newIntervalTime);
  }

  function getArrayToDraw(arr) {
    const doubledArray = arr.concat(arr).map((value) => ({
      value,
      disabled: false,
      show: false,
      // –°–æ–∑–¥–∞–µ–º —É–Ω–∞–∫–∞–ª—å–Ω—ã–µ –∫–ª—é—á—é, –∏–Ω–∞—á–µ –∫–∞—Ä—Ç–æ—á–∫–∏ –Ω–µ –±—É–¥—É—Ç –ø–µ—Ä–µ—Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è
      key: nanoid(),
    }));
    let j, temp;
    // –ü–µ—Ä–µ–º–µ—à–∏–≤–∞–µ–º –º–∞—Å—Å–∏–≤ –≤ —Å–ª—É—á–∞–π–Ω–æ–º –ø–æ—Ä—è–¥–∫–µ
    for (let i = doubledArray.length - 1; i > 0; i--) {
      j = Math.floor(Math.random() * (i + 1));
      temp = doubledArray[j];
      doubledArray[j] = doubledArray[i];
      doubledArray[i] = temp;
    }
    return doubledArray;
  }

  function handleCardClick(value, index) {
    clearTimeout(cardHideTimer);
    showCards(index);
    // –ï—Å–ª–∏ –æ—Ç–∫—Ä—ã–≤–∞–µ–º –≤—Ç–æ—Ä—É—é –∫–∞—Ä—Ç–æ—á–∫—É
    if (verifiable–°ard) {
      // –ï—Å–ª–∏ –∫–∞—Ä—Ç–æ—á–∫–∏ –æ–¥–∏–Ω–∞–∫–æ–≤—ã–µ
      if (verifiable–°ard.value === value) {
        increaseNumberMatches();
        disableCards(index, verifiable–°ard.index);
        updataVerifiableCard(null);
        if (numberMatches + 1 === emojiArray.length) {
          clearInterval(intervalTime);
          openWinPopup();
        }
      }
      // –ï—Å–ª–∏ –∫–∞—Ä—Ç–æ—á–∫–∏ —Ä–∞–∑–Ω—ã–µ
      else {
        disableAllCards();
        setTimeout(() => {
          hideCards(index, verifiable–°ard.index);
          updataVerifiableCard(null);
          enableAllCards();
        }, 700);
      }
    }
    // –ï—Å–ª–∏ –æ—Ç–∫—Ä—ã–≤–∞–µ–º –ø–µ—Ä–≤—É—é –∫–∞—Ä—Ç–æ—á–∫—É
    else {
      updataVerifiableCard({ value, index });
      const newCardHideTimer = setTimeout(() => {
        updataVerifiableCard(null);
        hideCards(index);
      }, 5000);
      setCardHideTimer(newCardHideTimer);
    }
  }

  return (
    <div className="app">
      <div className="app__cards-container">
        {cards.map((dataCard, i) => (
          <Card
            key={dataCard?.key || i}
            index={i}
            value={dataCard?.value || ''}
            disabled={dataCard?.disabled || disabledCards || false}
            show={dataCard?.show || false}
            handleCardClick={handleCardClick}
          />
        ))}
      </div>
      <div className="app__menu">
        <h1 className="app__title">–ò–≥—Ä–∞ "–ü–∞–º—è—Ç—å"</h1>

        <Button onClick={startGame} title="–°—Ç–∞—Ä—Ç" />
        <p className="app__timer">–¢–∞–π–º–µ—Ä: {msToTime(pastTime)}</p>
        <Button onClick={() => openResultPopup()} title="–¢–∞–±–ª–∏—Ü–∞ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤" />
      </div>
      <ResultPopup />
      <WinPopup />
    </div>
  );
}

const mapStateToProps = (state) => ({
  cards: state.cards.cards,
  verifiable–°ard: state.gameParams.verifiable–°ard,
  numberMatches: state.gameParams.numberMatches,
  disabledCards: state.app.disabledCards,
  pastTime: state.timer.pastTime,
});

const mapDispatchToProps = {
  updataCards,
  updataPastime,
  updataVerifiableCard,
  showCards,
  disableCards,
  disableAllCards,
  enableAllCards,
  hideCards,
  increaseNumberMatches,
  openResultPopup,
  openWinPopup,
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
