import { results } from '../utils/constants';
import { ADD_TO_RESULTS } from './types';

const initState = {
  results: results.sort((a, b) => a.time - b.time),
};

export const resultsReducer = (state = initState, action) => {
  switch (action.type) {
    case ADD_TO_RESULTS:
      return {
        ...state,
        // –ï—Å–ª–∏  –º–∞—Å—Å–∏–≤ results –±—É–¥–µ—Ç –æ—á–µ–Ω—å –±–æ–ª—å—à–æ–π,
        // –Ω—É–∂–Ω–æ –±—É–¥–µ—Ç –≤—Å—Ç–∞–≤–ª—è—Ç—å –Ω–æ–≤—ã–π —ç–ª–µ–º–µ–Ω—Ç –ø–æ –¥—Ä—É–≥–æ–º—É (–Ω–∞–ø—Ä–∏–º–µ—Ä –±–∏–Ω–∞—Ä–Ω—ã–º –ø–æ–∏—Å–∫–æ–º)
        results: [...state.results, action.payload].sort((a, b) => a.time - b.time),
      };

    default:
      return state;
  }
};
