import React from 'react';
import { connect } from 'react-redux';
import Popup from '../Popup/Popup';
import Button from '../Button/Button';
import { closeWinPopup, addToResults, openResultPopup } from '../../redux/actions';
import { msToTime } from '../../utils/msToTime';
import './WinPopup.css';

function WinPopup({ isOpenWinPopup, closeWinPopup, pastTime, addToResults, openResultPopup }) {
  const [name, setName] = React.useState('');

  function handleClick() {
    if (name.trim()) {
      addToResults({ name, time: pastTime });
      closeWinPopup();
      openResultPopup();
      setName('');
    }
  }
  return (
    <Popup title="–ü–æ–∑–¥—Ä–∞–≤–ª—è—é, –≤—ã –ø–æ–±–µ–¥–∏–ª–∏" isOpen={isOpenWinPopup} close={closeWinPopup}>
      <p className="popup__text">–í–∞—à —Ä–µ–∑—É–ª—å—Ç–∞—Ç: {msToTime(pastTime)}</p>
      <input
        value={name}
        onChange={(e) => setName(e.target.value)}
        className="popup__input"
        placeholder="–í–≤–µ–¥–∏—Ç–µ —Å–≤–æ–µ –∏–º—è"
        maxLength="15"
      />
      <Button title="–î–æ–±–∞–≤–∏—Ç—å –≤ —Ç–∞–±–ª–∏—Ü—É" onClick={handleClick} isSmallTitle={true} />
    </Popup>
  );
}
const mapStateToProps = (state) => ({
  isOpenWinPopup: state.app.isOpenWinPopup,
  pastTime: state.timer.pastTime,
});

const mapDispatchToProps = {
  closeWinPopup,
  addToResults,
  openResultPopup,
};

export default connect(mapStateToProps, mapDispatchToProps)(WinPopup);
